.statistics {
    width: 325px;
    text-align: center;
    padding-top: 20px;
    box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%), 0px 2px 1px rgb(0 0 0 / 20%);
    border-radius: 0px 0px 4px 4px;
    background-color: #ffffff;
}
.title {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.18;
    margin-bottom: 20px;
}